import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { SpListResponse } from "../../Model/Response/SpListResponse";
import { SpResponse } from "../../Model/Response/SpResponse";
import { SpViewModel } from '../../Model/ViewModel/SpViewModel';
import { StringViewModel } from "../../Model/ViewModel/StringViewModel";
import { TableSetServicesProvider } from '../table-set-services/table-set-services';
/*
  Generated class for the SpServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class SpServicesProvider  extends TableSetServicesProvider {
  private Sps: SpListResponse;
  private Sp: SpResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "Sp";
    console.log("Hello SpServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
     sub_url = "Sp?PageSize={1}&PageNumber={2}&keyword={3}&order_type={4}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,order_type);
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.Sps = new SpListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.Sps;
    });
  }



  public PostPutAsync(SpViewModel:SpViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "Sp";
    else
    sub_url = "Sp/{1}";
    sub_url = String.Format(sub_url, this.ctl,SpViewModel.exec_table_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,SpViewModel,mode).map(
      item => {
        this.Sp = new SpResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Sp;
      }
    );
  }

  public ExecuteAsync(StringViewModel:StringViewModel) {
    var sub_url: string;
    sub_url = "Sp/Execute";
    return this.Services.PostAsync(sub_url, this.ctl, true,StringViewModel).map(
      item => {
        this.Sp = new SpResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });

        return this.Sp;
      }
    );
  }


  public DeleteAsync(exec_table_seq:string) {
    var sub_url: string;
    sub_url = "Sp/{1}";
    sub_url = String.Format(sub_url, this.ctl,exec_table_seq);

    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.Sp = new SpResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.Sp;
      }
    );
  }





}



